import React from 'react';
import './Coin.css';

const Coin = ({ name, image, symbol, price, volume, priceChange, marketcap, lastUpdated }) => {
    const ISOdate = lastUpdated;
    const localDate = new Date(ISOdate).toLocaleString();

  return (
    <div className="coin-container">
      <div className="coin-row">
        <div className="coin">
          <img src={image} alt="crypto" />
          <h1>{name}</h1>
          <p className="coin-symbol">{symbol}</p>
        </div>
        <div className="coin-data">
          <p className="coin-price">${price}</p>
          <p className="coin-volume">${volume.toLocaleString()}</p>
          {priceChange < 0 ? (
            <p className="coin-percent red">{priceChange === null ? 'Not available' : priceChange.toFixed(2)}%</p>
          ) : (
            <p className="coin-percent green">{priceChange === null ? 'Not available' : priceChange.toFixed(2)}%</p>
          )}

          <p className="coin-marketcap">Mkt Cap: ${marketcap.toLocaleString()}</p>
          <p className="coin-lastUpdated">{localDate}</p>
        </div>
      </div>
    </div>
  );
};

export default Coin;
